
.fl {
  float: left;
}
.fr {
  float: right;
}
.clearfix {
  clear: both;
}
.g-mt10 {
  margin-top: 10px;
}
.g-mt15 {
  margin-top: 15px;
}
.g-mt20 {
  margin-top: 20px;
}
.g-mr10 {
  margin-right: 10px;
}
.g-mb10 {
  margin-bottom: 10px;
}
.m-title {
  padding: 10px 0 20px 0;
  border-bottom: 1px solid #eee;
  font-size: 14px;
  font-weight: 700;
  color: #3B89C0;
}
.m-content {
  margin-top: 30px;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity .1s ease;
  transition: opacity .1s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.status-success {
  color: #19be6b;
}
.status-wait {
  color: #ff9900;
}
.status-fail {
  color: #ed3f14;
}
.status-disable {
  color: #ccc;
}



