
.f-fuzzy-query {
  width: 100%;
  height: 100%;
  position: relative;
}
.f-fuzzy-query .query-select-dropdown {
  width: inherit;
  max-height: 200px;
  overflow: auto;
  margin: 5px 0;
  padding: 5px 0;
  background-color: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
          box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
  position: absolute;
  z-index: 900;
}
.f-fuzzy-query .query-select-dropdown.hidden {
  display: none;
}
.f-fuzzy-query .query-select-loading {
  text-align: center;
  color: #bbbec4;
}
.f-fuzzy-query .query-select-not-found {
  text-align: center;
  color: #bbbec4;
}
.f-fuzzy-query .query-select-item {
  margin: 0;
  line-height: normal;
  padding: 7px 16px;
  clear: both;
  color: #495060;
  font-size: 12px!important;
  white-space: nowrap;
  list-style: none;
  cursor: pointer;
  -webkit-transition: background 0.2s ease-in-out;
  transition: background 0.2s ease-in-out;
}
.f-fuzzy-query .query-select-item-focus,
.f-fuzzy-query .query-select-item:hover {
  background: #f3f3f3;
}
.f-fuzzy-query .query-select-item-selected,
.f-fuzzy-query .query-select-item-selected:hover {
  color: #fff;
  background: #3B89C0;
}



