
.m-product-info {
  background: #f5f5f5;
  border-radius: 4px;
  padding: 20px;
}
.m-operation button,
.m-operation a {
  min-width: 80px;
  margin-left: 10px;
}
.instruction-editor {
  background: #fff;
}
.instruction-editor .ql-toolbar {
  border-radius: 4px 4px 0 0;
  border: 1px solid #dddee1;
}
.instruction-editor .ql-container {
  min-height: 500px;
  border-radius: 0 0 4px 4px;
  border: 1px solid #eee;
}
.instruction-editor .ql-container .ql-editor {
  padding-bottom: 20px;
}



