
.g-layout {
  position: relative;
  min-width: 1200px;
}
.layout-left {
  height: 100%;
  width: 200px;
  position: absolute;
  z-index: 10;
  left: 0;
  background: #4d5256;
  transition: width 0.2s ease-in-out;
  -moz-transition: width 0.2s ease-in-out;
  /* Firefox 4 */
  -webkit-transition: width 0.2s ease-in-out;
  /* Safari and Chrome */
  -o-transition: width 0.2s ease-in-out;
  /* Opera */
}
.layout-right {
  overflow: hidden;
  min-height: 100vh;
  position: relative;
  width: 100%;
  padding-left: 200px;
  transition: padding-left 0.2s ease-in-out;
  -moz-transition: padding-left 0.2s ease-in-out;
  /* Firefox 4 */
  -webkit-transition: padding-left 0.2s ease-in-out;
  /* Safari and Chrome */
  -o-transition: padding-left 0.2s ease-in-out;
  /* Opera */
}
.g-layout.layout-mini .layout-left {
  width: 68px;
}
.g-layout.layout-mini .layout-right {
  padding-left: 68px;
}
.g-breadcrumb {
  padding: 10px 15px 0;
}
.g-content {
  min-height: 84vh;
  margin: 15px;
  background: #fff;
  border-radius: 4px;
  padding: 15px;
  position: relative;
}
.content-main {
  padding: 10px;
}
.copyright {
  width: 100%;
  text-align: center;
  padding: 10px 15px;
  color: #9ea7b4;
  background: #eee;
}



